// frontend/src/lib/axiosAuth.ts
import axios, { AxiosError, AxiosInstance, AxiosRequestConfig, AxiosRequestHeaders } from "axios";

const API_BASE = (process.env.NEXT_PUBLIC_API_URL || "").replace(/\/$/, ""); // no trailing slash
const ACCESS_TOKEN_KEY = "fahari_access_token";

/**
 * Safely read access token from localStorage (guard for SSR).
 */
function getAccessToken(): string | null {
  try {
    if (typeof window === "undefined") return null;
    return window.localStorage.getItem(ACCESS_TOKEN_KEY);
  } catch {
    return null;
  }
}

export function setAccessToken(token: string | null) {
  try {
    if (typeof window === "undefined") return;
    if (token === null) {
      window.localStorage.removeItem(ACCESS_TOKEN_KEY);
    } else {
      window.localStorage.setItem(ACCESS_TOKEN_KEY, token);
    }
  } catch {
    // ignore storage errors
  }
}

export function clearAccessToken() {
  setAccessToken(null);
}

const axiosAuth: AxiosInstance = axios.create({
  baseURL: API_BASE || undefined,
  withCredentials: true,
  headers: {
    "Content-Type": "application/json",
  },
});

type RetryConfig = AxiosRequestConfig & { _retry?: boolean };

axiosAuth.interceptors.request.use((config) => {
  const token = getAccessToken();
  if (token) {
    // Ensure headers is a valid AxiosRequestHeaders object
    config.headers = (config.headers ?? {}) as AxiosRequestHeaders;
    config.headers["Authorization"] = `Bearer ${token}`;
  }
  return config;
});

axiosAuth.interceptors.response.use(
  (resp) => resp,
  async (err: AxiosError) => {
    const originalRequest = err.config as RetryConfig | undefined;

    if (!originalRequest) return Promise.reject(err);

    if (err.response?.status === 401 && !originalRequest._retry) {
      originalRequest._retry = true;

      try {
        const refreshRes = await axios.post(
          `${API_BASE || ""}/api/admin/refresh`,
          {},
          { withCredentials: true }
        );

        const newToken = refreshRes?.data?.token as string | undefined;

        if (newToken) {
          setAccessToken(newToken);

          // ✅ Safe headers assignment with proper type
          originalRequest.headers = (originalRequest.headers ?? {}) as AxiosRequestHeaders;
          originalRequest.headers["Authorization"] = `Bearer ${newToken}`;

          return axiosAuth.request(originalRequest);
        } else {
          clearAccessToken();
          return Promise.reject(err);
        }
      } catch (refreshError) {
        clearAccessToken();
        return Promise.reject(refreshError);
      }
    }

    return Promise.reject(err);
  }
);

export default axiosAuth;
