//frontend\src\app\admin\jobs\page.tsx
"use client";

import React, { useState, useEffect } from "react";
import { useQuery, useMutation, useQueryClient } from "@tanstack/react-query";
import axios from "axios";
import { io, Socket } from "socket.io-client";
import { Button } from "../../../components/ui/button";
import { Card, CardContent } from "../../../components/ui/card";
import { Input } from "../../../components/ui/input";
import {
  Table,
  TableBody,
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  TableHead,
  TableHeader,
  TableRow,
  TableHead as TH,
} from "../../../components/ui/table";
import { PlusCircle } from "lucide-react";
import { motion } from "framer-motion";
import JobRow from "./JobRow";
import JobModal from "./JobModal";
import type { Job } from "./types";

let socket: Socket;

export default function JobsPage() {
  const [search, setSearch] = useState("");
  const [openModal, setOpenModal] = useState(false);
  const [editingJob, setEditingJob] = useState<Job | null>(null);
  const queryClient = useQueryClient();

  const API_BASE = process.env.NEXT_PUBLIC_API_URL || "";

  // Initialize Socket.IO only once
  useEffect(() => {
    if (!socket) {
      socket = io(API_BASE);
    }

    socket.on("job-updated", (updatedJob: Job) => {
      queryClient.setQueryData<Job[]>(["admin-jobs", search], (old = []) => {
        const index = old.findIndex((j) => j._id === updatedJob._id);
        if (index >= 0) old[index] = updatedJob;
        else old.push(updatedJob);
        return [...old];
      });
    });

    socket.on("job-deleted", (deletedJobId: string) => {
      queryClient.setQueryData<Job[]>(["admin-jobs", search], (old = []) =>
        old.filter((j) => j._id !== deletedJobId)
      );
    });

    return () => {
      socket.off("job-updated");
      socket.off("job-deleted");
    };
  }, [queryClient, search, API_BASE]);

  // Fetch jobs
  const {
    data: jobs = [],
    isLoading,
    isError,
    error,
  } = useQuery<Job[]>({
    queryKey: ["admin-jobs", search],
    queryFn: async () => {
      const res = await axios.get<{ data: Job[] }>(
        `${API_BASE}/api/careers/jobs?q=${encodeURIComponent(search)}`
      );
      return res.data?.data ?? [];
    },
  });

  // Delete job mutation
  const deleteJobMutation = useMutation<void, unknown, string>({
    mutationFn: async (id: string) => {
      await axios.delete(`${API_BASE}/api/careers/jobs/${encodeURIComponent(id)}`);
    },
    onSuccess: () => queryClient.invalidateQueries({ queryKey: ["admin-jobs", search] }),
  });

  const handleOpenModal = (job: Job | null = null) => {
    setEditingJob(job);
    setOpenModal(true);
  };

  return (
    <motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }} className="p-6 space-y-6">
      {/* HEADER */}
      <div className="flex items-center justify-between">
        <h1 className="text-2xl font-bold">Jobs Management</h1>
        <Button className="flex items-center gap-2" onClick={() => handleOpenModal()}>
          <PlusCircle size={18} /> Add Job
        </Button>
      </div>

      {/* MAIN CARD */}
      <Card className="shadow-md">
        <CardContent className="p-4 space-y-4">
          {/* SEARCH */}
          <Input
            placeholder="Search jobs by title or location..."
            value={search}
            onChange={(e) => setSearch(e.target.value)}
          />

          {/* LOADING */}
          {isLoading && <p className="text-green-500">Loading jobs...</p>}

          {/* ERROR */}
          {isError && (
            <div className="text-red-600">
              <p>Failed to load jobs.</p>
              {axios.isAxiosError(error) && (
                <p className="text-sm">{error.response?.data?.message || error.message}</p>
              )}
              <Button
                variant="outline"
                size="sm"
                className="mt-2"
                onClick={() => queryClient.invalidateQueries({ queryKey: ["admin-jobs", search] })}
              >
                Retry
              </Button>
            </div>
          )}

          {/* NO RESULTS */}
          {!isLoading && !isError && jobs.length === 0 && (
            <p className="text-green-500">No jobs found.</p>
          )}

          {/* JOB LIST */}
          {!isLoading && jobs.length > 0 && (
            <div className="overflow-x-auto">
              <Table>
                <TableHeader>
                  <TableRow>
                    <TH>Title</TH>
                    <TH>Location</TH>
                    <TH>Type</TH>
                    <TH>Status</TH>
                    <TH>Deadline</TH>
                    <TH>Actions</TH>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {jobs.map((job, idx) => {
                    const key = job._id ?? `job-${idx}`;
                    return (
                      <JobRow
                        key={key}
                        job={job}
                        onEdit={() => handleOpenModal(job)}
                        onDelete={() => {
                          const id = job._id;
                          if (!id) return;
                          deleteJobMutation.mutate(id);
                        }}
                      />
                    );
                  })}
                </TableBody>
              </Table>
            </div>
          )}
        </CardContent>
      </Card>

      {/* Job Modal */}
      <JobModal
        isOpen={openModal}
        onClose={() => setOpenModal(false)}
        initialData={editingJob}
        onSaved={() => queryClient.invalidateQueries({ queryKey: ["admin-jobs", search] })}
      />
    </motion.div>
  );
}
